import React, { Component } from 'react';
import RecordRTC from '../../RecordRTC';
import { Mutation } from 'react-apollo';
import { InMemoryCache } from 'apollo-cache-inmemory';
import { createUploadLink } from 'apollo-upload-client';
import { ApolloClient } from "apollo-client";
import { ApolloProvider } from "react-apollo";
import { gql } from 'apollo-boost';
import AddonText from '../addons/addon-text.component';
import AddonImage from '../addons/addon-img.component';
import AddonSidebar from '../addons/addon-sidebar.component';
import Flag from '../addons/addon-flag.component'

// styles and icons
import { Row, Col } from 'react-bootstrap';
import AlbumIcon from '@material-ui/icons/Album';
import StopIcon from '@material-ui/icons/Stop';
import Loader from 'react-loader-spinner';
import './video-recorder.style.css';
import flag from '../../images/flag.png';

// this component represents the video recorder frame of the create and edit visume page

export default class VideoRecorder extends Component {

    constructor(props) {
        super(props);
        this.state = {
            textAddonsInputs : [],
            imageAddonsInputs : []
        }
    }
    textAddonsInputs = []; 
    imageAddonsInputs = [];
    // call back function to get data from child to parent
    callback = (dataFromChild, status) => {
        if (status === "add") {
            if (dataFromChild.text) {
                this.textAddonsInputs.push(dataFromChild);
                this.setState({ ...this.state, textAddonsInputs: this.textAddonsInputs, viewText: null, viewImg: null, startTime: 0 })
            }
            else {
                this.imageAddonsInputs.push(dataFromChild);
                this.setState({ ...this.state, imageAddonsInputs: this.imageAddonsInputs, viewText: null, viewImg: null, startTime: 0 })
            }
            this.hideAddonsBtns();
        }
        else if (status === "delete") {
            this.handelDelete(dataFromChild);
            this.hideAddonsBtns();
        }
        else if (status === "view") {

            // set flag
            let flagBtn = document.querySelector('#FlagBtn').classList;
            let video = document.querySelector('video');
            
            flagBtn.add("bg-orange");
            video.pause();

            // show addons
            this.showAddonsBtns();
            // set data
            if (dataFromChild.text) {
                this.setState({ ...this.state, viewText: dataFromChild, viewImg: '', startTime: dataFromChild.start_time })
            }
            else {
                this.setState({ ...this.state, viewImg: dataFromChild, viewText: '' })
            }
        }
        else if (status === "edit") {
            if (dataFromChild.text) {
                // delete the previouse variable 
                this.handelDelete(this.state.viewText);
                // add the new value
                this.textAddonsInputs.push(dataFromChild);
                this.setState({ ...this.state, textAddonsInputs: this.textAddonsInputs, viewText: null })
            }
            else {
                // delete the previouse variable 
                this.handelDelete(this.state.viewImg);

                this.imageAddonsInputs.push(dataFromChild);
                this.setState({ ...this.state, imageAddonsInputs: this.imageAddonsInputs, viewImg: null })
            }
            this.hideAddonsBtns();
        }
        else if (status === "flag") {
            this.setState({ ...this.state, start_time: dataFromChild });
            this.showAddonsBtns();
        } 
        else if (status === "remove flag") {
            this.setState({ ...this.state, viewText: null, viewImg: null })
            this.hideAddonsBtns();
        }
        else if (status === "reset all"){
            this.setState({ ...this.state, imageAddonsInputs : [] ,textAddonsInputs: [], viewText: null, viewImg: null, startTime: 0 })
            this.textAddonsInputs = []; 
            this.imageAddonsInputs = [];
        }
    }
    showAddonsBtns = () =>{
        document.querySelector('.addonTextAndImage').classList.remove('d-none');
    }
    hideAddonsBtns = ()=>{
        document.querySelector('.addonTextAndImage').classList.add('d-none');
    }
    handelDelete = (item) => {
        let imgAddonsTemp = this.state.imageAddonsInputs;
        let textAddonsTemp = this.state.textAddonsInputs;

        if (item.text) {
            textAddonsTemp.splice(textAddonsTemp.indexOf(item), 1);
        } else {
            imgAddonsTemp.splice(imgAddonsTemp.indexOf(item), 1);
        }

        this.setState({ ...this.state, textAddonsInputs: textAddonsTemp, imageAddonsInputs: imgAddonsTemp, viewImg: '', viewText: ''});
    }
    componentWillReceiveProps(props) {
        if (props !== this.props) {
            this.setState({ ...this.state, bioId: props.data.id, data: props.data });

            // set video src if found 
            if (props.data) {
                let video = document.querySelector('video');
                if (props.data.video) {
                    video.setAttribute("src", props.data.video.url);
                    this.showFlag();
                } else {
                    this.hideFlag();
                    video.removeAttribute("src");
                    video.load();
                }
            }
        }
    }
    componentDidMount() {
        let video = document.querySelector('video');
        let that = this;
        let timeLimit;
        // open camera
        function captureCamera(callback) {
            navigator.mediaDevices.getUserMedia({ audio: true, video: true }).then(function (camera) {
                callback(camera);
            }).catch(function (error) {
                alert('Unable to capture your camera. Please check console logs.');
                console.error(error);
            });
        }
        // stop recording function
        function stopRecordingCallback() {

            // to fix video seeking issues
            RecordRTC.getSeekableBlob(recorder.getBlob(), function (seekableBlob) {
                video.src = URL.createObjectURL(seekableBlob);
            });

            video.src = video.srcObject = null;
            video.controls = true;
            video.muted = false;
            video.volume = 1;
            video.src = URL.createObjectURL(recorder.getBlob());

            // convert blob to file
            let file = new File([recorder.getBlob()], "videoname", {
                type: "video/webm",
            });
            that.setState({ ...this.state, file: file })
            if (that.state.file) {
                document.getElementById("btn-save-video").click();
            }
            recorder.camera.stop();
            recorder.destroy();
            recorder = null;
            window.clearTimeout(timeLimit);
        }
        var recorder; // globally accessible
        // start recording button on click
        document.getElementById('btn-start-recording').onclick = function () {

            // hide flag btn while recording
            that.hideFlag();

            this.disabled = true;
            video.autoplay = true;
            video.controls = false;
            captureCamera(function (camera) {
                video.muted = true;
                video.volume = 0;
                video.srcObject = camera;

                recorder = RecordRTC(camera, {
                    type: 'video',
                    canvas: {
                        width: 320,
                        height: 220
                    }

                });

                recorder.startRecording();

                // release camera on stopRecording
                recorder.camera = camera;

                document.getElementById('btn-stop-recording').disabled = false;
                document.getElementById('btn-stop-recording').classList.remove("d-none");
                document.getElementById('btn-start-recording').classList.add("d-none");
            });

            // stop recording after 1 minute
            timeLimit =  setTimeout(function () {    
                    document.getElementById('btn-stop-recording').disabled = true;
                    document.getElementById('btn-stop-recording').classList.add("d-none");
                    video.autoplay = false;
                    document.getElementById('btn-start-recording').disabled = false;
                    document.getElementById('btn-start-recording').classList.remove("d-none");
                    recorder.stopRecording(stopRecordingCallback);
            },62000)
        };
        // stop recording btn on click
        document.getElementById('btn-stop-recording').onclick = function () {
            this.disabled = true;
            video.autoplay = false;
            document.getElementById('btn-start-recording').disabled = false;
            document.getElementById('btn-stop-recording').classList.add("d-none");
            document.getElementById('btn-start-recording').classList.remove("d-none");
            recorder.stopRecording(stopRecordingCallback);
            // show flag btn
            that.showFlag();

        };

        // show flag addon
        this.showFlag()
    }
    showFlag =()=>{
            document.querySelector('.addonFlag').classList.remove('d-none');
    }
    hideFlag =()=>{
            document.querySelector('.addonFlag').classList.add('d-none');
    }
    render() {
        // activate uploading link on the oppollo 
        const apolloCache = new InMemoryCache({
            addTypename: false
        })
        const token = (localStorage.getItem('jwt')) ? JSON.parse(localStorage.getItem('jwt')).BearerTokenResponse.access_token : '';

        const uploadLink = createUploadLink({
            uri: 'https://spreago.black-squares.com/graphql',
            headers: {
                authorization: token ? `Bearer ${token}` : ""
            }
        })
        const client = new ApolloClient({
            cache: apolloCache,
            link: uploadLink
        })
        return (
            <div>
                <ApolloProvider client={client}>
                    <Mutation mutation={UPLOAD_FILE_STREAM}>
                        {(videoUpload, { data, loading }) => {
                            return (
                                <form className="videoContainer" encType={'multipart/form-data'}>
                                    <button
                                        hidden
                                        type="button"
                                        id="btn-save-video"
                                        className="btn-orange"
                                        onClick={() => {
                                            const file = this.state.file;
                                            const id = this.props.data.id;
                                            file && videoUpload({
                                                variables: { file: file, question_id: id }
                                            }).then((res, loading) => {
                                                if (res.data.videoUpload.Status.code) {
                                                    //let uploaded = document.querySelector("[data-uploaded=\"" + id + "\"]");
                                                   // uploaded.classList.remove("d-none");
                                                }
                                            }).catch((err) => this.setState({ ...this.state, ConnectionErorr: "Please try again" }));
                                        }}>save</button>
                                    {loading &&
                                        <div className="loading">
                                            <p className="text-white">Please wait...</p>
                                            <Loader
                                                type="TailSpin"
                                                color="white"
                                                height={50}
                                                width={50}
                                            />
                                        </div>
                                    }
                                </form>)
                        }}
                    </Mutation>
                    <div className="">
                        <Row className="m-0 rounded mb-1">
                            <Col md={10} xs={12} className="p-0 shadow">
                                <div className="p-relative video-addons-container ">
                                    <div className="videoCustom-container">
                                        <video 
                                            controls
                                            id="video"  
                                                controlslist="nodownload" 
                                                width="100%" 
                                                style={{ transition: "0.5s"}} 
                                                className="border-0 bg-black  border-0">
                                        </video>
                                    </div>
                                    <div className="addonFlag d-none">
                                        <Flag
                                            startTime={this.state.start_time} 
                                            callbackFromParent={this.callback}
                                        />
                                    </div>
                                    <div className="addonTextAndImage d-none">
                                        <AddonText 
                                            startTime={this.state.start_time} 
                                            callbackFromParent={this.callback} 
                                            viewText={this.state.viewText}
                                        />
                                        <AddonImage 
                                            startTime={this.state.start_time} 
                                            callbackFromParent={this.callback} 
                                            viewImg={this.state.viewImg}
                                        />
                                    </div>
                                </div>
                            </Col>
                            <Col md={2} xs={12} className="p-0 shadow">
                                <AddonSidebar 
                                    question={this.state.data}
                                    callbackFromParent={this.callback}  
                                    textAddons={this.state.textAddonsInputs} 
                                    imgAddons={this.state.imageAddonsInputs}
                                />
                            </Col>
                        </Row>
                        <Row className="justify-content-center m-0" >
                            <Col className="p-0">
                                <div className="m-auto btn-record-group bg-dark pr-3 pl-3">
                                    <button className="border-0 bg-transparent text-white pr-2" id="btn-start-recording">
                                            <div className="d-inline-block mr-1" style={{ borderRadius: "50%", background: "gray", padding: "3px", height: "30px", width: "30px" }}>
                                                <AlbumIcon className="text-red text-center" />
                                            </div>
                                            Start
                                    </button>
                                        <button
                                            className="border-0 bg-transparent text-white pl-2 d-none"
                                            id="btn-stop-recording" disabled>
                                            <div className="d-inline-block mr-1" style={{ borderRadius: "50%", background: "gray", padding: "3px", height: "30px", width: "30px" }}>
                                                <StopIcon className="text-red" />
                                            </div>
                                            Stop
                                    </button>
                                </div>
                            </Col>
                        </Row>
                    </div>

                </ApolloProvider>
            </div>
        )
    }
}

const UPLOAD_FILE_STREAM = gql`
mutation(
    $file        : Upload !
    $question_id : Int    !
) {
    videoUpload (
        Inputs : {
            question:{
                id : $question_id
            }
            videoInputs : {
                file : $file
            }
        }
    ) {
        Status {
            code
            title
            message
            check
        }
        question{
            title
            bio{
                title
            }
            video{
                url
                frame{
                    url
                  }
                type{
                    name
                }
            }
        }
    }
}
`;
