import React, { Component } from 'react';

//styles and icons
import { Row, Col } from 'react-bootstrap';
import FormatAlignLeftIcon from '@material-ui/icons/FormatAlignLeft';
import FormatAlignCenterIcon from '@material-ui/icons/FormatAlignCenter';
import FormatAlignRightIcon from '@material-ui/icons/FormatAlignRight';
import FormatItalicIcon from '@material-ui/icons/FormatItalic';
import FormatBoldIcon from '@material-ui/icons/FormatBold';
import SaveChanges from '../addons/addons-save.component';
import './addons.style.css';


// this component represents the text-editor addon of the video recorder

export default class TextEditor extends Component {

    constructor() {
        super();
        this.state = [];
    }
    callback = (data) => {
        if (data === 'save') 
        {
            this.saveData();
            this.handleOnclick();
            this.hideAddonsBtns()

        } else if (data === 'clear') {
            this.handleOnclick();
            this.hideAddonsBtns()
            this.viewText = null;
            this.props.callbackFromParent(0 ,"remove flag");
        }
    }
    handleOnclick = () => {
        // hide img editor
        this.closeImgEditor();
        // delete show value
        this.viewText  = null;
        document.querySelector('.text-editor-container').classList.toggle('d-none');
        document.querySelector('.text-input-container').classList.toggle('d-none');

        this.resetData();        
    }
    handleChangeInput = (e) => {
        document.querySelector('.addon-text .saveChanges').classList.remove('d-none');
        if(e.target.value.length < 1){
            document.querySelector('.addon-text .saveChanges').classList.add('d-none');
        }
    }
    handleAlignLeft = () => {
        
        document.querySelector('#text-input').classList.toggle('text-left');
        document.querySelector('.text-editor-left').classList.toggle('text-orange');

        document.querySelector('#text-input').classList.remove('text-right');
        document.querySelector('.text-editor-right').classList.remove('text-orange');
        document.querySelector('#text-input').classList.remove('text-center');
        document.querySelector('.text-editor-center').classList.remove('text-orange');
    }
    handleAlignRight = () => {
        document.querySelector('#text-input').classList.toggle('text-right');
        document.querySelector('.text-editor-right').classList.add('text-orange');

        document.querySelector('#text-input').classList.remove('text-left');
        document.querySelector('.text-editor-left').classList.remove('text-orange');
        document.querySelector('#text-input').classList.remove('text-center');
        document.querySelector('.text-editor-center').classList.remove('text-orange');
    }
    handleAlignCenter = () => {
        document.querySelector('#text-input').classList.toggle('text-center');
        document.querySelector('.text-editor-center').classList.add('text-orange');

        document.querySelector('#text-input').classList.remove('text-right');
        document.querySelector('.text-editor-right').classList.remove('text-orange');
        document.querySelector('#text-input').classList.remove('text-left');
        document.querySelector('.text-editor-left').classList.remove('text-orange');
    }
    handleItalic = () => {
        document.querySelector('#text-input').classList.toggle('font-italic');
        document.querySelector('.text-editor-italic').classList.toggle('text-orange');
    }
    handleBold = () => {
        document.querySelector('#text-input').classList.toggle('font-weight-bold');
        document.querySelector('.text-editor-bold').classList.toggle('text-orange');
    }
    resetData = () => {
        // hide save changes buttons
        document.querySelector('.addon-text .saveChanges').classList.add('d-none');
        // reset all data
        let btns = document.querySelectorAll('.text-editor-btn');
        for (let i = 0; i < btns.length; i++) {
            btns[i].classList.remove('text-orange');
        }
        document.querySelector("#text-input").value = ""
        document.querySelector("#text-input").classList = ""
    }
    saveData = () => {
        // hide save changes buttons
        document.querySelector('.addon-text .saveChanges').classList.add('d-none');

        let textValue = document.querySelector("#text-input").value;
        let startTime = this.startTime 
        let finishTime = startTime + 5;
        let classes = document.querySelector("#text-input").classList;
        let textAlign = 'left', TextStyle = 'normal', TextWeight = 'normal';
        let element = document.getElementById("text-input");
        let elementRelative = document.querySelector('.video-addons-container');
        let position_x = 0, position_y = 0;

        // get x and y position of text
        while (element && element !== elementRelative && !isNaN(element.offsetLeft) && !isNaN(element.offsetTop)) {
            position_x += element.offsetLeft - element.scrollLeft + element.clientLeft;
            position_y += element.offsetTop - element.scrollTop + element.clientTop;
            element = element.offsetParent;
        }
        // get styles
        for (let i = 0; i < classes.length; i++) {
            console.log(classes)
            if (classes[i] === 'text-left') textAlign = 'left';
            else if (classes[i] === 'text-right') textAlign = 'right';
            else if (classes[i] === 'text-center') textAlign = 'center';
            else textAlign = 'left';

            if (classes[i] === 'font-italic') TextStyle = 'italic'
            else TextStyle = 'normal'
            if (classes[i] === 'font-weight-bold') TextWeight = 'bold' 
            else TextWeight = 'normal'
        }
        // set all styles in textAddonsInput variable
        let textAddonInput = {
            start_time: startTime, finish_time: finishTime,
            text: textValue, position_x: position_x, position_y: position_y,
            text_align: textAlign, text_weight: TextWeight,
            text_style: TextStyle, text_size: 17,
            padding_top: 5, padding_bottom: 5,
            padding_left: 6, padding_right: 6,
            width: 342, height: 116
        }
        console.log(textAddonInput)
        // send data to parent using callback function
        if(textAddonInput){
            if(!this.viewText){
                this.props.callbackFromParent(textAddonInput , "add");
            }else{
                this.props.callbackFromParent(textAddonInput , "edit");
            }
        }

        // reset data 
        this.resetData();
    }
    closeImgEditor = () => {
        document.querySelectorAll('.img-output-container').forEach(e => e.remove());
        document.querySelector('.addon-images .saveChanges').classList.add('d-none');
    }
    showData = (textInfo) =>{
        // clse img editor if open
        this.closeImgEditor();
        // open text editor
        document.querySelector('.text-editor-container').classList.remove('d-none');
        document.querySelector('.text-input-container').classList.remove('d-none');
        // show save changes btns
        document.querySelector('.addon-text .saveChanges').classList.remove('d-none');

        // set the value in text area .
        document.querySelector("#text-input").value = textInfo.text;

        switch (textInfo.text_align) {
            case "center":
                this.handleAlignCenter()
                break;
            case "left":
                this.handleAlignLeft()
                break;
            case "right":
                this.handleAlignRight()
                break;
            default:
                this.handleAlignLeft()
                break;
        }
        // set styles
        if(textInfo.text_style === "italic"){
            this.handleItalic();
        }
        if(textInfo.text_weight === "bold"){
            this.handleBold();
        }
    }
    hideAddonsBtns = ()=>{
        document.querySelector('.addonTextAndImage').classList.add('d-none');

        let flagBtn = document.querySelector('#FlagBtn');
        flagBtn.classList.remove("bg-orange");
    }
    viewText = null;
    startTime = 0;
    componentWillReceiveProps(props){
        if(props !== this.props){
            if(props.viewText){
                this.showData(props.viewText);
                this.viewText = props.viewText
                if(props.startTime){
                    this.startTime = props.startTime
                }
            }
            else if (props.startTime){
                this.startTime = props.startTime
            }
            else{
                // hide text editor
                document.querySelector('.text-editor-container').classList.add('d-none');
                document.querySelector('.text-input-container').classList.add('d-none');
                this.resetData();
            }
        }
    }
    render() {
        return (
            <div className="addon-text">
                <div
                    className="p-absolute w-100 text-left"
                    style={{ top: '4rem', right: '1rem' }}
                    >
                    <Row className="row m-0 justify-content-end align-items-center">
                        <Col xl={4} lg={5} md={6} xs={8} className="p-0 d-none text-editor-container text-center">
                            <div className="bg-dark-transperant">
                                <button
                                    className="text-editor-btn text-editor-left border-0 bg-transparent text-white p-2 text-center"
                                    onClick={this.handleAlignLeft}
                                >
                                    <FormatAlignLeftIcon className="icon-responsive" />
                                </button>
                                <button
                                    className="text-editor-btn text-editor-center border-0 bg-transparent text-white p-2 text-center"
                                    onClick={this.handleAlignCenter}
                                >
                                    <FormatAlignCenterIcon className="icon-responsive" />
                                </button>
                                <button
                                    className="text-editor-btn text-editor-right border-0 bg-transparent text-white p-2 text-center"
                                    onClick={this.handleAlignRight}
                                >
                                    <FormatAlignRightIcon className="icon-responsive" />
                                </button>
                                <button
                                    className="text-editor-btn text-editor-italic border-0 bg-transparent text-white p-2 text-center"
                                    onClick={this.handleItalic}
                                >
                                    <FormatItalicIcon className="icon-responsive" />
                                </button>
                                <button
                                    className="text-editor-btn text-editor-bold border-0 bg-transparent text-white p-2 text-center"
                                    onClick={this.handleBold}
                                >
                                    <FormatBoldIcon className="icon-responsive" />
                                </button>
                            </div>
                        </Col>
                        <Col xl={1} md={2} xs={3} className="text-center">
                            <button
                                id='TextEditorBtn'
                                style={{ height: '3rem', width: '3rem', fontSize: '1.2rem' }}
                                className="rounded-circle bg-dark-transperant p-2 border-0 text-white icon-responsive"
                                onClick={this.handleOnclick}
                            > Aa
                            </button>
                        </Col>
                    </Row>
                </div>
                <div
                    className="p-absolute w-100 d-none text-input-container"
                    style={{ bottom: '3rem' }}>
                    <Row className="row m-0 justify-content-center align-items-center">
                        <Col md={7} xs={8}>
                            <div
                                className=" w-100 h-100 text-white p-relative"
                                style={{ height: '5rem' }}
                            >
                                <button
                                    type="button"
                                    className="close text-white p-absolute"
                                    aria-label="Close"
                                    style={{ top: '-9px', left: '-5px', zIndex: '1' }}
                                    onClick={this.handleOnclick}
                                >
                                    <span aria-hidden="true">&times;</span>
                                </button>
                                <textarea
                                    id="text-input"
                                    rows="3"
                                    className="addon-textarea"
                                    placeholder="write your comment ..."
                                    maxLength="160"
                                    onChange={this.handleChangeInput}
                                />
                            </div>
                        </Col>
                    </Row>
                </div>
                <SaveChanges callbackFromParent={this.callback} />
            </div>
        )
    }
}